import React from "react";
import styled from "styled-components";

const Container = styled.div`
  height: 300px;
  display: flex;
  flex-direction: column;
`;

const ThumbnailWrapper = styled.div`
  flex: 1;
`;

const NoImageTitle = styled.div`
  display: flex;
  font-size: 24px;
  font-weight: 500;
  background-color: var(--pre);
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 8px 8px 0 0;
`;

const ContentWrapper = styled.div`
  padding: 18px;
  background-color: #2c2c2c;
  border-radius: 0 0 8px 8px;
`;

const ContentTitle = styled.h3`
  font-size: 20px;
  margin-top: 4px;
`;

const ContentDescription = styled.p`
  font-size: 14px;
  padding: 8px 0;
  word-wrap: break-word;
  word-break: keep-all;
`;

const ContentDate = styled.span`
  font-size: 12px;
  color: #8c8c8c;
`;

type Props = {
  title: string;
  date: string;
  tags: string[];
  description?: string;
};

const BlogListItem = (props: Props) => {
  const { title, date, tags, description } = props;

  return (
    <Container>
      <ThumbnailWrapper>
        <NoImageTitle>
          {title.length > 20 ? `${title.slice(0, 20)}...` : title}
        </NoImageTitle>
        {/* <img alt="이미지 들어갈 자리" /> */}
      </ThumbnailWrapper>
      <ContentWrapper>
        {/* <div>{tags.join(",")}</div> */}
        <div>
          <ContentTitle>{title}</ContentTitle>
          <ContentDescription>
            {description && description.length > 60
              ? `${description.slice(0, 61)}...`
              : description}
          </ContentDescription>
          <ContentDate>{date}</ContentDate>
        </div>
      </ContentWrapper>
    </Container>
  );
};

export default BlogListItem;
